<template>
  <div class="chat-component">
    <!-- ËøôÈáåÊòØ‰∏Ä‰∏™Á§∫‰æãÂå∫ÂüüÔºåÁî®Êù•ËÆ©Áî®Êà∑ËæìÂÖ•API Key -->
    <div class="debug-settings">
      <span style="color: red;">[debug_area]:</span>
      <input v-model="apiKeyInput" placeholder="Âú®Ê≠§Á≤òË¥¥‰Ω†ÁöÑ OpenAI API Key" />
      <button @click="handleSetApiKey">‰øùÂ≠ò Key</button>
      <button @click="handleCopyPrompt">Copy Prompt</button>
    </div>

    <!-- Ê∂àÊÅØÂå∫ -->
    <div class="messages-container" ref="messagesContainer">
      <!-- ‰ΩøÁî® v-for Ê∏≤ÊüìÊâÄÊúâÊ∂àÊÅØ -->
      <div v-if="messages.length > 2">
        <div v-for="(message, index) in messages" :key="index">
          <!-- GPT Ê∂àÊÅØÔºöÂ§¥ÂÉè + ËìùÊ°ÜÂπ∂Âàó -->
          <template v-if="message.sender === 'gpt' && message.display">
            <div class="gpt-message-container">
              <img :src="gptMessageIcon" alt="ChatGPT Â§¥ÂÉè" class="chatgpt-message-icon" />
              <div class="message gpt">
                <span>{{ extractMessage(message.text) }}</span>
              </div>
            </div>
          </template>

          <!-- Áî®Êà∑Ê∂àÊÅØÔºàmeÔºâ -->
          <template v-else-if="message.sender === 'me' && message.display">
            <div class="me-message-container">
              <div class="message me">
                <span>{{ message.text }}</span>
              </div>
            </div>
          </template>

          <!-- Á≥ªÁªüÊ∂àÊÅØÔºàsystemÔºâ -->
          <!-- <template v-else-if="message.sender === 'system'">
          <div class="message system">
            <span>{{ message.text }}</span>
          </div>
        </template> -->

          <!-- ÈÄâÊã©Ê∂àÊÅØÔºàchoiceÔºâÔºåÁî®Êà∑ÂèØ‰ª•ÁÇπÂáª‚ÄúOK‚ÄùÊàñËÄÖ‚ÄúÊàëËßâÂæóËøò‰∏çÂ§ü‚Äù -->
          <template v-else-if="message.sender === 'choice' && message.display">
            <div class="choice-message-container">
              <div class="message choice">
                <span>Ê†πÊçÆÂ∞ë‰æ†Êèê‰æõÁöÑ‰ø°ÊÅØÔºåËèåËèåÂ∑≤Áªè<span style="color: var(--color-primary); font-weight: bold;">Â∏Æ‰Ω†ÊÄªÁªì</span>‰∫Ü‰ª•‰∏ãË¶ÅÁÇπ:
                </span>

                <div class="item-content-item" v-for="(point, i2) in getContentsFromMessage(message)" :key="i2">
                  <div class="bullet-point-content">
                    <span class="bullet-point">¬∑ {{ point.bullet_point }}:</span>
                    <span class="bullet-content">{{ point.content }}</span>
                  </div>
                </div>

                <div class="choice-buttons">
                  <button class="choice-button-ok" @click="handleOk(message)">Ê≤°ÈóÆÈ¢ò, Â±ïÁ§∫Âà∞Âè≥ËæπÂêß~! üéâ</button>
                  <button class="choice-button-not-enough" @click="handleNotEnough">ÊàëËßâÂæóËøò‰∏çÂ§ü, ÁªßÁª≠ÂØπËØùÂêß:(</button>
                </div>
              </div>
            </div>
          </template>
        </div>
      </div>
      <div class="chat-loading-container" v-else>
        <l-waveform class="chat-loading-icon" size="60" stroke="3.5" speed="1" color="var(--color-primary)"></l-waveform>
      </div>
    </div>

    <div class="gradient-overlay"></div>

    <!-- ËæìÂÖ•Âå∫ÔºàÂèëÈÄÅÁªôChatGPTÔºâ -->
    <div class="input-area-container" v-if="isWaitingForAIResponse">
      <div class="loading-container">
        <l-infinity size="20" stroke="4" stroke-length="0.15" bg-opacity="0.5" speed="1.3"
          color="var(--color-secondary)"></l-infinity>
      </div>
    </div>
    <div v-else class="input-area-container">
      <div class="input-area-left">
        <input v-model="inputValue"
          :placeholder="'ËØ∑Êé¢ËÆ®Âíå ‚Äú' + (currentSelectedTitle ? currentSelectedTitle : 'ÂΩìÂâçÊ®°Âùó') + '‚Äù ÊúâÂÖ≥ÁöÑ‰∫ãÊÉÖ'"
          class="chatgpt-input" @keyup.enter="handleSendMessage" />
      </div>
      <div class="chatgpt-send-button" @click="handleSendMessage">
        <img :src="sendIcon" alt="ChatGPT ÂõæÊ†á" class="chatgpt-send-icon" />
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, watch, nextTick, defineProps, defineEmits, onMounted } from 'vue'
import ChatgptModel from '@/models/chatgpt_model.js'
import { infinity } from 'ldrs'
import { waveform } from 'ldrs'
waveform.register()
infinity.register()

// Áà∂ÁªÑ‰ª∂ÈúÄ‰º†ÂÖ•
const props = defineProps({
  modules: {
    type: Array,
    default: () => []
  },
  currentSelectedTitle: {
    type: String,
    default: ''
  }
})

// Âè™ÂêëÁà∂ÁªÑ‰ª∂ÂèëÂ∞Ñ ‚Äúupdate-resume‚Äù ‰∫ã‰ª∂
const emit = defineEmits(['update-resume'])

// ChatGPT ÂÆû‰æã (Âçï‰æã)
const chatgptInstance = ChatgptModel.getInstance()

// Áî®Êà∑ËæìÂÖ•ÁöÑ API Key
const apiKeyInput = ref('')

// Âú®ÁªÑ‰ª∂ÂàùÂßãÂåñÊó∂ÔºåÂèØ‰ª•‰ªé localStorage ‰∏≠Â∞ùËØïËØªÂèñ
onMounted(() => {
  const storedKey = localStorage.getItem('openaiApiKey')
  if (storedKey) {
    apiKeyInput.value = storedKey
    // ÂêåÊó∂Áªô ChatgptModel ËÆæÁΩÆ
    chatgptInstance.setApiKey(storedKey)
  }

  if (props.currentSelectedTitle) {
    initChat()
  }
})

function initChat() {
  if (messages.value.length == 1) {
    const { type, title } = activeModule.value
    chatgptInstance.sendMessage(
      type,
      title,
      "ÊàëÁé∞Âú®ÂºÄÂßãËÆ®ËÆ∫Ëøô‰∏™ËØùÈ¢òÔºåËØ∑‰Ω†Áî®‰∏ÄÂè•ËØùÂºïÂØºÊàë",
      false
    )
  }
}

// ÁÇπÂáª‚Äú‰øùÂ≠ò Key‚ÄùÊåâÈíÆÊó∂ÔºåÊää key Â≠òÂà∞Âçï‰æã
function handleSetApiKey() {
  chatgptInstance.setApiKey(apiKeyInput.value)
  // ÂêåÊó∂Â≠òÂà∞ localStorageÔºàÂèØÈÄâÔºâ
  localStorage.setItem('openaiApiKey', apiKeyInput.value)
  alert('Â∑≤ËÆæÁΩÆ API Key')
}

// ËæìÂÖ•Ê°ÜÂÜÖÂÆπ
const inputValue = ref('')

// ÂèëÈÄÅÊåâÈíÆÂõæÊ†á
const sendIcon = require('@/assets/icon/chatgpt-send-icon.svg')

// GPT Ê∂àÊÅØÂ§¥ÂÉè
const gptMessageIcon = require('@/assets/icon/logo1.png')

// Ê∂àÊÅØÊªöÂä®ÂÆπÂô®
const messagesContainer = ref(null)

/**
 * ËÆ°ÁÆóÔºöÊ†πÊçÆ currentSelectedTitleÔºåÊâæÂà∞ÂØπÂ∫îÊ®°Âùó
 */
const activeModule = computed(() => {
  return (
    props.modules.find((m) => m.title === props.currentSelectedTitle) ||
    { type: '', title: '' }
  )
})

/**
 * Ê†πÊçÆ activeModule ËÆ°ÁÆóÂá∫ÂØπÂ∫îÁöÑÊ∂àÊÅØ
 */
const messages = computed(() => {
  const { type, title } = activeModule.value
  if (!type || !title) return []
  return chatgptInstance.getMessagesForTitle(type, title)
})

const isWaitingForAIResponse = computed(() => {
  return messages.value.length > 1 && messages.value[messages.value.length - 1].sender === 'me'
})

function handleSendMessage() {
  const trimmedValue = inputValue.value.trim()
  if (!trimmedValue) return

  const { type, title } = activeModule.value
  chatgptInstance.sendMessage(type, title, trimmedValue, true)

  // Ê∏ÖÁ©∫ËæìÂÖ•Ê°Ü
  inputValue.value = ''
}

/**
 * Ëß£Êûê GPT Ê∂àÊÅØ‰∏≠ÁöÑ JSON Âπ∂ÊèêÂèñ message Â≠óÊÆµ
 */
function extractMessage(gptText) {
  try {
    const parsed = JSON.parse(gptText)
    return parsed.message || '(Êú™ÊâæÂà∞ message Â≠óÊÆµ)'
  } catch (error) {
    return gptText
  }
}

/**
 * ÁõëÂê¨ messages ÂèòÂåñÔºå‰∏ÄÊó¶ GPT ÂèëÊù•Êñ∞Ê∂àÊÅØÔºåÂ∞±Ê£ÄÊü• is_enough ÈÄªËæëÔºõ
 * Â¶ÇÊûú is_enough=trueÔºåÂàôÊèíÂÖ•‰∏Ä‰∏™‚Äúchoice‚ÄùÁ±ªÂûãÁöÑÊ∂àÊÅØÔºåËÆ©Áî®Êà∑ÈÄâÊã©
 */
watch(
  () => messages.value,
  async (newMessages) => {
    if (!newMessages || newMessages.length === 0) return

    const lastMessage = newMessages[newMessages.length - 1]
    if (lastMessage.sender === 'gpt') {
      try {
        const parsedData = JSON.parse(lastMessage.text)
        if (parsedData.is_enough) {
          // Êñ∞Â¢ûÔºöÂæÄÊ∂àÊÅØÂàóË°®ÈáåÂÜçÊèíÂÖ•‰∏Ä‰∏™ choice Ê∂àÊÅØÔºàÂíåËÅäÂ§©ÂÜÖÂÆπÂêåÁ∫ßÔºâ
          const { type, title } = activeModule.value
          chatgptInstance.addMessage(type, title, {
            sender: 'choice',
            text: JSON.stringify({ meta_data: parsedData.meta_data }),
            display: true
          })
        }
      } catch (e) {
        console.error('GPT ËøîÂõûÊ∂àÊÅØ‰∏çÊòØÊ†áÂáÜ JSON ÊàñËß£ÊûêÂ§±Ë¥•Ôºö', e)
      }
    }

    await nextTick()
    if (messagesContainer.value) {
      messagesContainer.value.scrollTop = messagesContainer.value.scrollHeight
    }
  },
  { deep: true }
)


/**
 * ÁõëÂê¨ currentSelectedTitle ÁöÑÂèòÂåñ
 */
watch(
  () => props.currentSelectedTitle,
  (newTitle, oldTitle) => {
    console.log(`Ê®°Âùó‰ªé "${oldTitle}" ÂàáÊç¢Âà∞ "${newTitle}"`)
    initChat()
  }
)

/**
 * Áî®Êà∑ÁÇπÂáª‚ÄúOK‚ÄùÊó∂Ë∞ÉÁî®
 */
function handleOk(choiceMessage) {
  try {
    const parsed = JSON.parse(choiceMessage.text)
    // Â¶ÇÊûúËß£ÊûêÊàêÂäüÔºåÊãøÂà∞ meta_dataÔºåÂèëÂ∞Ñ‰∫ã‰ª∂
    emit('update-resume', parsed.meta_data)
    // emit('close-chat')

    const { type, title } = activeModule.value
    chatgptInstance.sendMessage(
      type,
      title,
      "ÊàëÁé∞Âú®ËßâÂæóOK‰∫ÜÔºåÂ∑≤ÁªèÈÄâÊã©‰∫ÜÊÄªÁªìÂà∞Âè≥Ëæπ! ‰Ω†ÂèØ‰ª•ÁªßÁª≠Ë∑üÊàëËÅäÂ§©ÔºåÂèëÁé∞Ëøô‰ªΩÁªèÂéÜÁöÑÂÖ∂‰ªñ‰∫ÆÁÇπÔºåis_enough ËØ∑ÂÖàËÆæÁΩÆ‰∏∫ false",
      false
    )
  } catch (e) {
    console.error('choiceMessage.text ‰∏çÊòØ JSON', e)
  }
}

function getContentsFromMessage(message) {
  const parsed = JSON.parse(message.text)
  return parsed.meta_data.resumeData.content
}

/**
 * Áî®Êà∑ÁÇπÂáª‚ÄúÊàëËßâÂæóËøò‰∏çÂ§ü‚ÄùÊó∂Ë∞ÉÁî®
 */
function handleNotEnough() {
  const predefinedMessage = 'ÊàëËÆ§‰∏∫ÊÄªÁªìËøò‰∏çÂ§üÔºåËØ∑ÁªßÁª≠ÂØπËØù'

  const { type, title } = activeModule.value
  if (!type || !title) {
    console.error('ÂΩìÂâçÊú™ÈÄâÊã©ÊúâÊïàÁöÑÊ®°Âùó')
    return
  }

  // ÂèëÈÄÅÈ¢ÑÂÆö‰πâÊ∂àÊÅØÂà∞ GPT
  chatgptInstance.sendMessage(type, title, predefinedMessage, true)

  // ÂèØÈÄâÔºöÊ∏ÖÁ©∫ËæìÂÖ•Ê°ÜÔºàÂ¶ÇÊûúÈúÄË¶ÅÔºâ
  inputValue.value = ''

  // ÂèØÈÄâÔºöÊèêÁ§∫Áî®Êà∑Ê∂àÊÅØÂ∑≤ÂèëÈÄÅ
  console.log('Â∑≤ÂèëÈÄÅÈ¢ÑÂÆö‰πâÊ∂àÊÅØÁªôGPT')
}
</script>

